import { Grid, Container } from "@material-ui/core";
import "./header.css";
import "./responsive.css";
import React, { useState } from 'react'
import { Link } from "react-router-dom";
import LOGO from "../Assets/logo.png";
import LOGO2 from "../Assets/logo2.png";
import MenuIcon from '@mui/icons-material/Menu';
import CloseIcon from '@mui/icons-material/Close';
import MobileNav from '../components/MobileNav';
import CallIcon from '@mui/icons-material/Call';
import FacebookOutlinedIcon from '@mui/icons-material/FacebookOutlined';


const Header = () => {

    const [openMenu, setOpenMenu] = useState(false);
    const [navBar, setnavBar] = useState(false);

    const hamburgerIcon =  <MenuIcon className={navBar ? 'icon active' : 'icon'} onClick={() => setOpenMenu(!openMenu)} />
    const closeIcon =  <CloseIcon className={navBar ? 'icon active' : 'icon'} onClick={() => setOpenMenu(!openMenu)} />

    const changeBg = () =>{
        if(window.scrollY >= 100){
            setnavBar(true);
        }
        else{
            setnavBar(false);
        }
    };

    window.addEventListener('scroll', changeBg);
    
    return(

       
        <Grid container className={navBar ? 'nav-head active' : 'nav-head'}>
            <Container maxWidth="xl">
            <Grid container className="m-flex">
               
                <Grid item sm={5} className="leftmenu-grid">
                    <div className="leftmenu">
                        <div className="social-icon">
                                <a href="tel:+923030303030">
                                    <span className="display-f">
                                        <FacebookOutlinedIcon />
                                    </span> 
                                </a>
                                <a href="tel:+923030303030">
                                    <span className="display-f">
                                        <CallIcon />
                                    </span> 
                                </a>
                                <a href="tel:+923030303030">
                                    <span className="display-f">
                                        <CallIcon />
                                    </span> 
                                </a>
                        </div>
                        <Link to="/">Home</Link>
                        <Link to="/about">About us</Link>
                        <Link to="/services">Services</Link>

                    </div>
                </Grid>
                <Grid item sm={2}>
                    <div className="logo">

                        <Link to="/"><img src={LOGO} alt="LOGO" /></Link>
                        
                    </div>
                    <div className="logo-m">

                        <Link to="/"><img src={LOGO2} alt="LOGO" /></Link>
                        
                    </div>
                </Grid>
                <Grid item sm={5} className="rightmenu-grid">
                    <div className="rightmenu">

                        <Link to="/portfolio">Portfolio</Link>
                        <Link to="/teams">Team</Link>
                        <Link to="/contact">Contact</Link>
                        <div className="phone-icon">
                                <a href="tel:+923030303030">
                                    <span className="display-f">
                                        <CallIcon /> <span>+92 303 0303030</span>
                                    </span> 
                                </a>
                        </div>
                       
                        
                    </div>
                </Grid>
                
                
                <Grid item sm={10} className="hamburger" justifyContent="end">
                    <div className="hamburger">
                           {openMenu ? closeIcon : hamburgerIcon}
                           
                    </div>
                    
                </Grid>
                    {openMenu && <MobileNav />}
                

            </Grid>
            </Container>
        </Grid>
       
    );
}


export default Header;